export class CurrentMenuItemPropertyModel {

    constructor(urlHashPart, isFromBrowserAction, isOpenInNewWindow, isInLineUXApp) {
        this._urlHashPart = urlHashPart;
        this._isFromBrowserAction = isFromBrowserAction;
        if (!isFromBrowserAction) {
            this._isFromBrowserAction = false;
        }
        this._isOpenInNewWindow = isOpenInNewWindow;
        if (!isOpenInNewWindow) {
            this._isOpenInNewWindow = false;
        }
        this._isInLineUXApp = isInLineUXApp;
        if (!isInLineUXApp) {
            this._isInLineUXApp = false;
        }
    }

    getUrlHashPart() {
        return this._urlHashPart;
    }

    isFromBrowserAction() {
        return this._isFromBrowserAction;
    }

    isOpenInNewWindow() {
       return this._isOpenInNewWindow;
    }

    isInLineUXApp() {
       return this._isInLineUXApp;
    }
}